import React, { Component } from "react"

class Socials extends Component {
  render() {
    return (
      <section className="socials" id="socials">
        <h2 className="section__title">You can follow me on</h2>
        <div className="socials__icons-container">
          <a href="https://www.facebook.com/shadesofpixie" target="_blank">
            <img
              src="./img/facebook.png"
              alt="Facebook"
              srcset="./img/facebook.svg"
            />
          </a>
          <a href="https://github.com/shadesofpixie" target="_blank">
            <img
              src="./img/github.png"
              alt="Github"
              srcset="./img/github.svg"
            />
          </a>
          <a href="https://www.twitter.com/shadesofpixie" target="_blank">
            <img
              src="./img/twitter.png"
              alt="Twitter"
              srcset="./img/twitter.svg"
            />
          </a>
          <a href="https://www.instagram.com/shadesofpixiee" target="_blank">
            <img
              src="./img/instagram.png"
              alt="Instagram"
              srcset="./img/instagram.svg"
            />
          </a>
        </div>
      </section>
    )
  }
}

export default Socials
