import React, { Component } from "react"

class Thumbnail extends Component {
  render() {
    return (
      <div className="col-lg-3">
        <div className="thumbnail">
          <img
            className="img-fluid"
            src="./img/profilepic.png"
            alt="profile pic"
            style={{ width: 100 + "%" }}
          />
        </div>
      </div>
    )
  }
}

export default Thumbnail
